<template>
  <v-container fluid pt-0>
    <v-layout justify-center>
      <v-flex xs12 sm12 md9 lg6>
          <v-layout column mb-3 bw>
            <antibiogram-guideline></antibiogram-guideline>
            <v-dialog
              v-model="dialog"
              :fullscreen="$vuetify.breakpoint.xsOnly"
              max-width="500"
              persistent
            >
              <template v-slot:activator="{ on }">
                <v-btn
                color="primary"
                v-on="on">View Customized Antibiogram</v-btn>
              </template>
              <antibiogram-filter @close="dialog = false" @viewAntibiogram="viewAntibiogram"></antibiogram-filter>
            </v-dialog>
          </v-layout>
          <v-layout column>
            <v-flex mb-3 bw>
              <v-text-field
              v-model="searchFacility"
              label="Search Hospital for Antibiogram"
              placeholder="Start typing to search"
              :append-icon="inputIcon"
              @click:append-icon="searchFacility = ''"
              outline></v-text-field>
            </v-flex>
            <v-layout column="">
              <v-flex v-for="facility in filteredFacilities" :key="facility.id" mb-3 pa-2 card pcoh>
                  <v-layout justify-space-between align-center>
                    <v-tooltip left>
                      <template v-slot:activator="{ on }">
                        <v-btn v-if="facility.favourite" v-on="on" icon light @click="unStar(facility)">
                          <v-icon color="primary">star</v-icon>
                        </v-btn>
                        <v-btn v-else @click="star(facility)" icon light>
                          <v-icon>star_border</v-icon>
                        </v-btn>
                      </template>
                      <span v-if="!facility.favourite">Star Hospital</span>
                      <span v-else>Unstar Hospital</span>
                    </v-tooltip>
                    <v-flex xs12 pl-3 @click="viewAntibiogram(facility)">
                        <span class="subheading font-weight-light">{{facility.facilityName}}</span>
                    </v-flex>
                    <v-flex pl-4 @click="viewAntibiogram(facility)">
                        <v-icon>arrow_forward</v-icon>
                    </v-flex>
                  </v-layout>
              </v-flex>
            </v-layout>
          </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";
import { mapActions } from "vuex";
import AntibiogramFilter from "@/components/base/antibiogram-filter";

export default {
  components: { AntibiogramFilter },
  computed: {
    ...mapGetters(['facilities', 'staredHospital']),
    filteredFacilities() {
      return this.facilities.filter(facility => facility.facilityName.toLowerCase().includes(this.searchFacility.toLowerCase()))
    },
    inputIcon() {
      return this.searchFacility !== "" ? "clear" : "search";
    }
  },
  created(){
    this.getFacilities(1)
    console.log(this.facilities)
  },
  data() {
    return {
      dialog: false,
      showToolTip: false,
      searchFacility: ''
    }
  },
  methods: {
    ...mapActions(['getFacilities', 'updateStaredHospital']),
    goto (id) { this.$router.push(`/antimicrobials/${id}`) },
    viewAntibiogram(id) { this.$router.push(`/antibiogram`) },
    viewAntibiogram(facility) {
      this.$store.commit('updateAntibiogramFilter', {
        scope: "Facility/Hospital Level",
        duration: "1 year",
        values: [facility],
        advanced: false,
      })
      this.$router.push('/antibiogram')
    },
    star(facility) {
      this.updateStaredHospital({
        facility: facility,
        star: true,
      })
    },
    unStar(facility) {
      this.updateStaredHospital({
        facility: facility,
        star: false,
      })
    }
  }
};
</script>

